import { NextResponse } from 'next/server';
import { CARDS_DATA } from '@/lib/data';

export async function GET(
  request: Request,
  // Perhatikan tipe datanya berubah jadi Promise
  { params }: { params: Promise<{ id: string }> }
) {
  // WAJIB di-await dulu di Next.js 15
  const { id } = await params;

  // Debugging: Cek ID yang diterima (Opsional, hapus kalau udah jalan)
  console.log("Mencari ID:", id);

  // Cari kartu berdasarkan ID yang sudah di-await
  const card = CARDS_DATA.find((c) => c.id === id);

  if (!card) {
    return NextResponse.json({ message: 'Card not found' }, { status: 404 });
  }

  return NextResponse.json(card);
}